import React, { useState, useRef, forwardRef } from 'react';
import logo from './logo.svg';
import './App.css';
import { Navbar, Nav, NavDropdown, Container, Jumbotron  } from 'react-bootstrap';
import './Responsive.scss';
import { connect } from 'react-redux';

function App(props) {

  return (
    <div className="App">
      <Navbar bg="black" expand="lg" variant="dark" fixed=''>
        <Container>
          <Navbar.Brand href="#home">PortFolio</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link href="#home">Home</Nav.Link>
              <Nav.Link href="#link">Link</Nav.Link>
              <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Jumbotron className="background">
          <Categories categoryData={props.categoryData}/>
      </Jumbotron>
      <div className='copyright_background'></div>
    </div>
  );
}



const Category = forwardRef((props, ref) =>{

  return (
    <div className="category" id={props.id}>
      <div  ref={ref} >
        <h1 className='categoryTitle'>{props.categoryData[props.id].title}</h1>
      </div>
    </div>
  )

})

function Categories(props) {

  var categories = props.categoryData;

  const scrollRef = useRef(null);
  const categoryRef = useRef([]);
  const [isDrag, setIsDrag] = useState(false);
  const [startX, setStartX] = useState(0);
  const [currenIndex, setCurrentIndex] = useState(0);

  function isDragStart(e) {
    e.preventDefault();
    setIsDrag(true);
    setStartX(scrollRef.current.scrollLeft + e.pageX);
  }

  function isDragMove(e) {
    if(isDrag){
      const { scrollWidth, clientWidth, scrollLeft } = scrollRef.current;

      scrollRef.current.scrollLeft = startX - e.pageX;

      if(scrollLeft === 0){
        setStartX(e.pageX);
      } else if ( scrollWidth <= clientWidth + scrollLeft) {
        setStartX(scrollLeft + e.pageX)
      }
    }
  }

  function isDragEnd() {
    setIsDrag(false);
    //updateCurrentIndex();
    //scrollRef.current.scrollLeft = 200;
    //categoryRef.current[4].scrollIntoView({behavior: "smooth", block: "nearest", inline: "center"});
  }

  return (
    <div className='categories'
        onMouseDown={isDragStart}
        onMouseMove={(isDrag) ? isDragMove : null}
        onMouseUp={isDragEnd}
        onMouseLeave={isDragEnd}
        ref={scrollRef}
    >
      {
        categories.map(function(a, i){

          return <Category ref={e => (categoryRef.current[i] = e)} categoryData={categories} id={i} />
        })
      }
    </div>
  )
}

function getData(state){
  return {
    categoryData: state
  }
}

export default connect(getData)(App);
